import React from 'react';
import {TodoContext} from '../TodoContext'

function TodoForm(){
    const [newTodoValue, setNewTodoValue] = React.useState('');
    const {addTodo, setOpenModal,} = React.useContext(TodoContext);
    const onCancele = () => {setOpenModal(false)};
    const onChange = (event) => { setNewTodoValue(event.target.value)};
    const onSubmit = (event) => {event.preventDefault(); addTodo(newTodoValue); setOpenModal(false)};
    return (
        <form onSubmit={onSubmit}>
            <label>...</label>
            <textarea placeholder="Añade una tarea" value={newTodoValue} onChange={onChange}/>
            <div>
                <button onClick={onCancele} type="button">
                    Cancelar
                </button>
                <button type="submit">
                    Añadir
                </button>
            </div>
        </form>
    );
}

export {TodoForm}